// =============================================================================
// pow
// =============================================================================
///   
///
/// @param {Number, Length} $number - 
/// @param {Number, Length} $exponent - 
/// @return {Number} -  `$number`    `$exponent`

@function pow($number, $exponent) {
	$value: 1;

	@if $exponent > 0 {
		@for $i from 1 through $exponent {
			$value: $value * $number; } }

	@return $value; }

// =============================================================================
// round-float
// =============================================================================
///   n   
///
/// @param {Number, Length} $number - 
/// @param {Number, Length} $n -    
/// @return {Number} -    `n`   

@function round-float($number, $n) {
	$float: pow(10,$n);

	$value: round($number*$float)/$float;

	@return $value; }

/// Remove the unit of a length
/// @param {Number} $number - Number to remove unit from
/// @return {Number} - Unitless number
@function strip-unit($number) {
	@if type-of($number) == 'number' and not unitless($number) {
		@return $number / ($number * 0 + 1); }

	@return $number; }

///     
/// @param {Number} $number -  
/// @param {Number} $n -     
/// @return {Number} -  
@function get-catheter($number) {
	$unit: $number * 0 + 1;

	@return sqrt(pow(strip-unit($number), 2)/2)*$unit; }
